.btn {
  width: 100%;
  font-size: 16px;
  font-weight: 500;
  background-color: rgb(0, 0, 0);
  color: white;
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px 8px;
  transition: 0.3s;
}

@media (min-width: 1200px) {
  .btn:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
  }
}
