.lesson-card {
  position: relative;
  width: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  padding: 20px;
  background-color: white;
  border: 1px solid #c6c6c6;
  border-radius: 20px;
  box-shadow: 0px 0px 21px -4px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  transition: 0.3s;
}

.list-wrapp a {
  color: #03afbb;
  transition: 0.3s;
}

.lesson-card::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgb(221 234 255);
  top: 20px;
  right: 20px;
  transition: 0.3s;
}

.lesson-card.active::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgb(53, 212, 106);
  top: 20px;
  right: 20px;
}

.lesson-img {
  overflow: hidden;
  padding-top: 60%;
  position: relative;
}

.lesson-img img {
  height: 100%;
  object-fit: contain;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.lesson-option {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title-wrapp {
  margin-bottom: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.title-wrapp .title {
  margin-bottom: 10px;
}

.title h3 {
  font-size: 20px;
}

.plan-title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 10px;
  text-align: left;
}

.plan-list {
  margin-bottom: 20px;
  text-align: left;
}

.plan-list li {
  list-style: inside;
}

.plan-list li::marker {
  color: #96bbf5;
}

.lesson-links {
  margin-bottom: 10px;
  text-align: left;
}

.links-title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 10px;
}
.links-list {
  text-align: left;
}

form {
  display: flex;
  flex-direction: column;
}

form textarea {
  padding: 20px;
}

@media (min-width: 1200px) {
  .lesson-card:hover {
    box-shadow: 0px 0px 21px 4px rgba(0, 0, 0, 0.75);
  }

  .list-wrapp a:hover {
    color: rgb(12, 3, 134);
  }
}
